import axios from 'axios';
import { getDecodedToken } from './cookies';

// Defina a URL base da API
const apiUrl = process.env.REACT_APP_API_URL + '/api';

export const getMeetingsByDate = async (date) => {
    try {
        // Verifica se o date  um objeto Date vlido, seno, converte
        if (!(date instanceof Date)) {
            date = new Date(date); // Converte a data se no for um objeto Date
        }

        // Verifica se a data  vlida
        if (isNaN(date.getTime())) {
            throw new Error('Data invlida');
        }

        const formattedDate = date.toISOString().split('T')[0]; // Formata a data para o formato esperado

        const response = await axios.get(`${apiUrl}/meetings/getMeetingByDate`, {
            params: { date: formattedDate },
        });

        // Aqui, a resposta j contm a lista de reunies em response.data.meetings
        const meetings = response.data.meetings;

        if (meetings && meetings.length > 0) {
            console.log('Reunies encontradas:', meetings);
            return meetings;
        } else {
            console.log('Nenhuma reunio encontrada para esta data.');
            return []; // Retorna um array vazio caso no haja reunies
        }

    } catch (error) {
        console.error('Erro ao buscar reunies:', error);
        throw new Error('Erro ao buscar reunies. Tente novamente.');
    }
};



// Funo para buscar os detalhes de uma reunio
export const getMeetingDetails = async (meetingId) => {
    try {
        const locationResponse = await axios.get(`${apiUrl}/locations/getLocationByMeeting`, {
            params: { meetingId },
        });
        const location = locationResponse.data.location;

        const minutesResponse = await getMeetingMinutes(meetingId); // Funo corrigida
        const minutes = minutesResponse.data || [];

        const invitationResponse = await getInvitationsByMeeting(meetingId); // Funo corrigida
        const invitedUserIds = invitationResponse.data.map(invitation => invitation.id_usuario);

        const usersDetails = await Promise.all(
            invitedUserIds.map(async (userId) => {
                const userResponse = await getUserById(userId); // Funo corrigida
                return userResponse.data;
            })
        );

        return { location, minutes, usersDetails };
    } catch (error) {
        console.error('Erro ao buscar detalhes da reunio:', error);
        throw new Error('Erro ao buscar detalhes da reunio. Tente novamente.');
    }
};

