// Funo para buscar todos os usurios
export const getAllUsers = async () => {
    try {
        
        const response = await axios.get(`${apiUrl}/users/getAllUsers`);

        // Filtra o usurio logado da lista de usurios
        const token = getDecodedToken();
        const userId = token.id;
        const users = response.data.filter(user => user.id !== userId);

        return users;

    } catch (error) {
        console.error('Erro ao buscar usurios:', error);
        throw new Error('Erro ao buscar usurios. Tente novamente.');
    }
};

